.navbar {
    padding: 20px;
  }
  .navbar ul {
    overflow: hidden;
    text-align: center;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }
  .navbar ul li {
    display: inline-block;
    margin-right: 20px;
    padding: 0 20px;
    text-align: center;
    margin: 0 auto;
  }
  .navbar ul li a {
    font-family: "Source Serif Pro", serif;
    text-decoration: none;
    color: #9600fa;
    font-size: 20px;
    font-weight: bold;
  }
  .navbar ul li a:hover {
    color: #fff;
  }